body {
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f9;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url("./assets/img/background.png");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  overflow: hidden;
  transition: background 0.5s ease-in-out;
  color: white;
  box-sizing: border-box;
}

@keyframes backgroundAnimation {
  0% {
    background-color: #f4f4f9;
  }
  50% {
    background-color: #ffcccb;
  }
  100% {
    background-color: #f4f4f9;
  }
}

body {
  animation: backgroundAnimation 15s infinite alternate;
}

/* Contenedores principales */
.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.85);
  border-radius: 20px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
  padding: 40px;
  max-width: 480px;
  width: 100%;
  animation: fadeIn 0.6s ease-out;
  transition: transform 0.3s ease;
}

.form-container:hover {
  transform: translateY(-5px);
}

.form-container::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 8px;
  background: linear-gradient(90deg, #007bff, #e41c1c);
  bottom: 0;
  left: 0;
  transition: opacity 0.3s ease;
}

.form-container:hover::after {
  opacity: 0.8;
}

/* Logo */
#logologin {
  max-width: 280px;
  margin-bottom: 25px;
  transition: transform 0.3s ease, filter 0.3s ease;
  border-radius: 15px;
  filter: brightness(1);
}

#logologin:hover {
  transform: scale(1.1);
  filter: brightness(1.2);
}

/* Estilos del formulario */
.form-login {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 25px;
}

/* Campos de entrada */
.input-group label {
  font-size: 14px;
  color: #333;
  margin-bottom: 8px;
  font-weight: 500;
  text-transform: uppercase;
}

.input-group input {
  width: 100%;
  padding: 12px;
  border-radius: 8px;
  border: 1px solid #ddd;
  font-size: 14px;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.input-group input:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 0 8px rgba(0, 123, 255, 0.5);
}

/* Botón de inicio de sesión */
#passwordbutton {
  background-color: #007bff;
  color: white;
  padding: 12px 20px;
  border-radius: 8px;
  border: none;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s ease-in-out;
  box-shadow: 0 4px 8px rgba(0, 123, 255, 0.3);
  text-transform: uppercase;
}

#passwordbutton:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

#passwordbutton:active {
  transform: scale(1);
}

/* Mensaje de error */
.danger {
  color: red;
  font-size: 14px;
  text-align: center;
  margin-top: 20px;
  font-weight: bold;
  animation: slideUp 0.4s ease-out;
  line-height: 1.5;
}

/* Barra lateral */
.sidebar {
  position: fixed;
  top: 0;
  left: -300px;
  height: 100%;
  width: 300px;
  background-color: #fff;
  color: #333;
  transition: left 0.3s ease-in-out;
  padding-top: 60px;
  z-index: 1000;
  box-shadow: 3px 0 15px rgba(0, 0, 0, 0.2);
  overflow-y: auto;
  padding-bottom: 50px;
}

.sidebar.show {
  left: 0;
}

.sidebar .menu-item {
  color: #333;
  background-color: #f4f4f9;
  padding: 18px 22px;
  margin-top: 20px;
  margin-left: 20px;
  text-align: left;
  width: 80%;
  font-size: 14px;
  cursor: pointer;
  border-radius: 10px;
  transition: background-color 0.3s, color 0.3s, transform 0.3s ease;
}

.sidebar .menu-item:hover {
  background-color: #4b4b4b;
  color: white;
  transform: translateX(5px);
}

/* Icono de la barra lateral */
.show-sidebar-button {
  position: absolute;
  top: 20px;
  left: 15px;
  font-size: 28px;
  background-color: #fff;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  z-index: 2000;
  transition: color 0.3s ease, transform 0.2s ease;
}

.show-sidebar-button:hover {
  color: #00b7ff;
  transform: rotate(180deg);
  background-color: #f0f0f0;
}

/* Tarjeta principal */
.homepage-card {
  background: #fff;
  border-radius: 18px;
  box-shadow: 5px 5px 15px rgb(0, 0, 0);
  padding: 15px;
  margin-left: 100px;
  width: calc(100% - 200px);
  transition: margin-left 0.3s ease-in-out, box-shadow 0.3s ease;
  animation: fadeIn 1s ease-out;
}

.homepage-card:hover {
  box-shadow: 0 12px 36px rgba(0, 0, 0, 0.2);
}

/* Fecha */
.current-date {
  font-size: 20px;
  font-weight: bold;
  color: black;
  text-align: left;
  margin-top: 30px;
}

/* Botón para crear tabla */
.create-table-button {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s, transform 0.2s ease-in-out;
}

.create-table-button:hover {
  background-color: #218838;
  transform: scale(1.05);
}

.create-table-button:active {
  transform: scale(1);
}

.current-date:hover {
  transition: transform 0.2s ease, color 0.3s ease;
  color: #1b89ff;
}

/* Tabla */
.table-container {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  padding: 15px;
  background-color: #f9f9f9;
  border-radius: 12px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
  border-top: 3px solid #000000;
  animation: fadeIn 1s ease-out;
  display: block;
  margin: 0;
  margin-top: 15px;
  overflow-y: auto;
  max-height: 700px;
}

.table-container table {
  min-width: 700px;
  width: auto;
  border-collapse: collapse;
  font-size: 38px; /* Aumenté el tamaño de la fuente aún más */
  border-radius: 8px;
  table-layout: fixed;
}

th,
td {
  padding: 8px 6px;
  text-align: left;
  border-radius: 6px;
  font-weight: 400;
  word-wrap: break-word;
}

th {
  background-color: #007bff;
  color: white;
  padding: 10px;
  margin-left: 5px;
  margin-right: 5px;
  font-size: 14px;
  text-transform: uppercase;
}

td {
  padding: 12px;
  text-align: left;
  border-radius: 8px;
  background-color: #f9f9f9;
  box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.05);
}

td input {
  width: 100%;
  padding: 10px;
  border: 1px solid #007bff;
  border-radius: 6px;
  font-size: 12px;
  background-color: #fff;
  box-sizing: border-box;
  transition: border-color 0.3s, box-shadow 0.3s;
}

td input:focus {
  border-color: #28a745;
  box-shadow: 0 0 8px rgba(40, 167, 69, 0.3);
  outline: none;
}

td input:invalid {
  border-color: #dc3545;
}

select {
  width: 100%;
  padding: 9px;
  border: 1px solid #007bff;
  border-radius: 6px;
  background-color: #fff;
  font-size: 12px;
  box-sizing: border-box;
  transition: border-color 0.3s, box-shadow 0.3s;
}

select:focus {
  border-color: #28a745;
  box-shadow: 0 0 8px rgba(40, 167, 69, 0.3);
  outline: none;
}

option {
  width: 100%;
  padding: 14px;
  border: 2px solid #007bff;
  border-radius: 6px;
  background-color: #fff;
  font-size: 12px;
  box-sizing: border-box;
  transition: border-color 0.3s, box-shadow 0.3s;
}

tr:hover td {
  background-color: #f1f1f1;
}

/* Estilo para los botones */
button.delete-button {
  background-color: #ff6347; /* Color rojo para el botón */
  color: #fff;
  border: none;
  padding: 6px 12px;
  cursor: pointer;
  border-radius: 5px;
  font-size: 18px;
  transition: background-color 0.3s ease;
}

button.delete-button:hover {
  background-color: #ff3300; /* Rojo más oscuro al pasar el mouse */
}

